import React, { useState } from 'react';
import SectionWithSwiper from 'components/organisms/section/sectionWithSwiper';
import { SwiperSlide } from 'swiper/react';
import SwiperCore, { Mousewheel, Pagination } from 'swiper';
import Badge from 'components/atoms/badge';
import { ISKillsObject } from 'types/ISkill';
import Tooltip from 'components/atoms/tooltip';
import infoIcon from 'assets/icons/common/info.svg';
import SKILLS from './constants/ConstSkills';
import 'swiper/css';
import 'swiper/css/pagination';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';

SwiperCore.use([Pagination, Mousewheel]);

export default function Skills() {
  const SKILLS_CATEGORIES = Object.keys(SKILLS);

  const [currentCategory, setCurrentCategory] = useState(SKILLS_CATEGORIES[0]);

  const determineCurrentCategory = (index: number) => {
    setCurrentCategory(SKILLS_CATEGORIES[index]);
  };

  return (
    <div className="h-4/6 w-full">
      <SectionWithSwiper
        styles="mt-0"
        title={currentCategory}
        slidesPerView={1}
        slidesPerGroup={1}
        direction="vertical"
        mousewheel={{ forceToAxis: true, thresholdDelta: 50 }}
        pagination={{
          clickable: true,
        }}
        modules={[Pagination, Mousewheel]}
        onSlideChange={swiper => {
          determineCurrentCategory(swiper.activeIndex);
        }}>
        {SKILLS_CATEGORIES.map(category => (
          <SwiperSlide key={category} className="px-8 grid gap-x-8 items-center grid-cols-1 sm:grid-cols-2">
            {SKILLS[category as keyof ISKillsObject]?.map(skill => (
              <Accordion type="single" collapsible>
                <AccordionItem value={skill?.name} className="border-b-2 border-b-[rgba(255,255,255,0.15)]">
                  <AccordionTrigger>
                    <div className="flex items-center gap-1 sm:gap-2" key={skill.name}>
                      <img src={skill?.image} className="h-7 w-7 sm:h-12 sm:w-12" alt="Icon of skill" />
                      <div>
                        <div className="my-auto text-lg sm:text-2xl">{skill.name}</div>
                      </div>
                      <Badge
                        styles="my-auto ml-2 cursor-pointer"
                        text={`${skill.yearsOfExperience.toString()} YEAR${skill.yearsOfExperience > 1 ? 'S' : ''}`}
                      />
                    </div>
                  </AccordionTrigger>
                  <AccordionContent>{skill?.description}</AccordionContent>
                </AccordionItem>
              </Accordion>
            ))}
          </SwiperSlide>
        ))}
      </SectionWithSwiper>
    </div>
  );
}
